<template>
  <div class="wrap">
    <h1>5월 마지막주 영화 예매 순위aaaa</h1>
    <ul class="movies">
      <li v-for="movie in movies" class="item" v-bind:key="movie">
        <span class="rank">{{movie.id}}</span>
        <div class="detail">
          <strong class="tit">{{movie.name}}</strong>
          <span class="rate">예매율 <span class="num">{{movie.rate}}</span></span>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  created () {
    this.$http.get('/api/movies')
        .then((response) => {
          this.movies = response.data
        })
  },
  data () {
    return {
      movies: []
    }
  }
}
</script>