import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/HomeComponent.vue'
import About from './views/AboutComponent.vue'
import Test from '@/components/HelloWorld'

Vue.use(Router)
export const router = new Router({
 mode: 'history',
 routes: [
   {
     path: '/',
     name: 'index',
     component: Index
   },
   {
     path: '/about',
     name: 'about',
     component: About
   },
   {
    path: '/test',
    name: 'test',
    component: Test
  }
 ]
})